import React, { Component } from 'react'
import axios from 'axios'
import { faGoogle } from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
class GoogleLogin extends Component {
    constructor(props) {
        super(props)
        this.state = {
            calData: []
        }
        this.displayEvents.bind(this);
    }
    displayEvents =  ()=>{
        axios.get('/fetchEvents')
        .then(response => {
            let calData =[...this.state.calData];
            const resData=eval(response.data[0]);
            for(var i in resData.events){
                calData.push({eventName:resData.events[i], recurrence:resData.recurrence[i]})
            }           
            this.setState({calData : calData})
        })
        .catch(err=>console.log("dataE",err))
    }
    render() {
        const {calData} = this.state;
        const eventList = calData.map(cal=>
        <li class="mdc-list-item">
        <span class="mdc-list-item__ripple"></span>
        <span class="mdc-list-item__text">{cal.eventName} : {cal.recurrence}</span></li>)
        return (
            <div class="container mt-3 p-5 border bg-light">
                <form style={{display:"block"}} class="mx-auto" method="GET" action="/gauth">
                <button style={{display:"block"}} class="mx-auto mdc-button mdc-button--raised ">
                        <div class="mdc-button__ripple"></div>
                        <FontAwesomeIcon icon={faGoogle}/>&nbsp;
                        <span class="mdc-button__label contact">Login to Google</span>
                </button>
                </form>
                <br />
                <button style={{display:"block"}} class="mx-auto mdc-button mdc-button--raised" onClick={this.displayEvents}>
                <div class="mdc-button__ripple"></div>
                Show Google Calendar Events</button>
                <ul style={{display:"block"}} class="mdc-list">Events:</ul>
                {eventList}
            </div>
        )
    }
}

export default GoogleLogin
